@import url("https://fonts.googleapis.com/css2?family=Spline+Sans:wght@300;400;500;600;700&display=swap");

*,
*:after,
*:before {
  box-sizing: border-box;
}

:root {
  --c-white: #fff;
  --c-black: #000;

  --c-ash: #eaeef6;
  --c-charcoal: #a0a0a0;
  --c-void: #141b22;

  --c-fair-pink: #ffedec;
  --c-apricot: #fbc8be;
  --c-coffee: #754d42;
  --c-del-rio: #917072;

  --c-java: #1fcac5;

  --c-titan-white: #f1eeff;
  --c-cold-purple: #a69fd6;
  --c-indigo: #6558d3;
  --c-governor: #4133b7;
}

.cards {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  flex-wrap: wrap;
  gap: 2.5rem;
  width: 90%;
  max-width: 1000px;
  margin: 10vh auto;
}

.card {
  border-radius: 16px;
  box-shadow: 0 30px 30px -25px rgba(#4133b7, 0.25);
  max-width: 300px;
}

.plan {
  padding: 10px;
  background-color: var(--c-white);
  color: var(--c-del-rio);

  strong {
    font-weight: 600;
    color: var(--c-coffee);
  }

  .inner {
    padding: 20px;
    padding-top: 40px;
    background-color: var(--c-fair-pink);
    border-radius: 12px;
    position: relative;
    overflow: hidden;
    width: 260px;
  }

  .pricing {
    position: absolute;
    top: 0;
    right: 0;
    background-color: var(--c-apricot);
    border-radius: 99em 0 0 99em;
    display: flex;
    align-items: center;
    padding: 0.625em 0.75em;
    font-size: 1.25rem;
    font-weight: 600;
    color: var(--c-coffee);
    small {
      color: var(--c-del-rio);
      font-size: 0.75em;
      margin-left: 0.25em;
    }
  }

  .title {
    font-weight: 600;
    font-size: 1.25rem;
    color: var(--c-coffee);
    & + * {
      margin-top: 0.75rem;
    }
  }

  .info {
    & + * {
      margin-top: 1rem;
    }
    height: 200px;
    overflow: auto;
  }

  .features {
    display: flex;
    flex-direction: column;
    li {
      display: flex;
      align-items: center;
      gap: 0.5rem;
      & + * {
        margin-top: 0.75rem;
      }
    }

    .icon {
      background-color: var(--c-java);
      display: inline-flex;
      align-items: center;
      justify-content: center;
      color: var(--c-white);
      border-radius: 50%;
      width: 20px;
      height: 20px;
      svg {
        width: 14px;
        height: 14px;
      }
    }

    & + * {
      margin-top: 1.25rem;
    }
  }

  button {
    font: inherit;
    background-color: var(--c-indigo);
    border-radius: 6px;
    color: var(--c-white);
    font-weight: 500;
    font-size: 1rem;
    width: 100%;
    border: 0;
    padding: 1em;
    &:hover,
    &:focus {
      background-color: var(--c-governor);
    }
  }
  .button-container {
    display: flex;
    gap: 20px;
  }
}
